<template>
  <div class="pageContent">
    <!--<Form ref="formInline" :model="userInfo" :rules="ruleInfo">
      <div>
        <Poptip trigger="hover" title="" content="">
          <Form-item prop="userName">
            <Input type="text" v-model="userInfo.userName" placeholder="Username">
            <Icon type="ios-person-outline" slot="prepend"></Icon>
            </Input>
          </Form-item>
        </Poptip>
      </div>
      <Poptip trigger="hover" title="" content="">
        <Form-item prop="psw">
          <Input type="password" v-model="userInfo.psw" placeholder="Password">
          <Icon type="ios-locked-outline" slot="prepend"></Icon>
          </Input>
        </Form-item>
      </Poptip>
      <Poptip trigger="hover" title="" content="">
        <Form-item>
          <Button type="primary" @click="handleSubmit('formInline')"></Button>
        </Form-item>
      </Poptip>
    </Form>-->
    <Poptip class="inputDiv" trigger="hover" title="">
      <Input @on-enter="submitFun()" type="text" size="large" v-model="userInfo.userName" placeholder="Who are you?">
      <Icon type="ios-person-outline" slot="prepend"></Icon>
      <Button slot="append" icon="close" @click="userInfo.userName='';"></Button>
      </Input>
      <div slot="content">
        <br/>
        <span style="color:orangered;"></span>
      </div>
    </Poptip>
  </div>
</template>

<script>
  export default {
    name: 'hello',
    data () {
      return {
        userInfo: {
          userName: '',
          psw: ''
        },
        infoObj: {
          wyp: 'honey a',
          lfy: '',
          zn: '',
          zr: '',
          zpk: 'lulu'
        }
//        ,
//        ruleInfo: {
//          userName: [
//            {required: true, message: '', trigger: 'blur'}
//          ],
//          psw: [
//            {required: true, message: '', trigger: 'blur'},
//            {type: 'string', min: 6, message: '6', trigger: 'blur'}
//          ]
//        }
      }
    },
    methods: {
//      handleSubmit (name) {
//        this.$refs[name].validate((valid) => {
//          if (valid) {
//            this.$Message.success('!')
//          } else {
//            this.$Message.error('!')
//          }
//        })
//      },
      submitFun () {
        let userName = this.userInfo.userName.trim();
        if (userName in this.infoObj) {
          let info = this.infoObj[userName]
          this.$Notice.success({
            title: (userName === 'wyp') ? '' : '',
            desc: info,
            top: 100,
            duration: 10
          })
          setTimeout(() => {
            this.$router.push('/Home')
          }, 3000)
        } else {
          this.$Notice.error({
            title: 'notice',
            desc: '',
            top: 100,
            duration: 5
          })
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .pageContent {
    background: rgb(28, 36, 56);
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .inputDiv {
    height: 50px;
    line-height: 50px;
  }

</style>



